//GAMES REVIEWS

.aussie-casino {
  &__games-reviews {
    height: 100%;
    position: relative;
    padding-bottom: 278px;
    &::after {
      content: '';
      background: #FFBE1D;
      width: 100%;
      height: 575px;
      position: absolute;
      top: 0;
    }
    h2 {
      font-family: @montserrat-b;
      font-size: 73px;
      color: @c2;
      z-index: 2;
    }
  }

  &__games-reviews_wrap {
    max-width: 91%;
    margin-left: 0;
    margin-right: auto;
    z-index: 1;
    position: relative;
  }

  &__games-reviews_sort-post--wrap {
    max-width: 92%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
  }

  &__games-reviews_top--wrap {
    max-width: 92%;
    margin-left: auto;
    margin-right: auto;
    z-index: 2;
    position: relative;
    padding-top: 97px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 65px;
    select[name="categoryfiltergames"] {
      border: 1px solid rgba(0,0,0,.25);
      border-radius: 30px;
      width: 270px;
      height: 60px;
      font-family: @montserrat-b;
      font-size: 16px;
      color: @c2;
      text-transform: uppercase;
      letter-spacing: -1px;
      padding: 0 30px;
      background: transparent url(../img/arrow-bottom.svg) center no-repeat;
      background-position-x: 189px;
      background-position-y: 4px;
      appearance:none;
      cursor: pointer;
      transition: .5s;
    }
  }

  &__games-reviews_post--item-wrap1 {
    display: flex;
    width: 100%;
    justify-content: space-between;
    .aussie-casino__games-reviews_post--item {
      width: 711px;
      height: 500px;
      img {
        width: 711px;
        height: 500px;
        border-radius: 4px;
        border: 1px solid @c1;
        box-shadow: 0 3px 24px 0 #00000029;
      }
    }
  }

  &__games-reviews_post--item {
    width: 417px;
    height: 500px;
    img {
      width: 417px;
      height: 500px;
      border: 1px solid @c1;
      box-shadow: 0 3px 24px 0 #00000029;
      border-radius: 4px;
      object-fit: cover;
    }
  }

  &__games-reviews_post--item-wrap2 {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-top: 248px;
    flex-wrap: wrap;
  }
  &__games-reviews_post--item-6,
  &__games-reviews_post--item-7,
  &__games-reviews_post--item-8 {
    margin-top: 251px;
  }
  &__games-reviews_link {
    font-family: @montserrat-sb;
    font-size: 24px;
    color: @c2;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: .5s;
    margin-top: 30px;
    display: inline-block;

    &:hover {
      text-decoration-color: #000;
    }
  }
  &__games-reviews_wrap--img {
    position: relative;
  }
  &__games-reviews_date {
    position: absolute;
    right: -75px;
    bottom: 60px;
    transform: rotate(-91deg);
    font-family: @montserrat-r;
    font-size: 12px;
  }
  &__games-reviews_top--desc {
    font-family: @montserrat-r;
    font-size: 16px;
    color: @c2;
    opacity: 0.3;
    margin-right: 19px;
    letter-spacing: 0;
    font-weight: 600;
  }
}